<template>
  <Content class="user">
    <Breadcrumb>
      <BreadcrumbItem to="/">首页</BreadcrumbItem>
      <BreadcrumbItem>查询用户</BreadcrumbItem>
    </Breadcrumb>
    <div class="search">
      <Input style="width:460px" search v-model.trim="search" enter-button placeholder="姓名\邮箱"/>
    </div>
    <div class="uers-table">
      <Table :columns="columns" :data="fUsers"></Table>
    </div>
    <div class="user-page">
      <Page
        :total="length"
        :current.sync="current"
        show-elevator
        size="small"
        :page-size="pageSize"
      />
    </div>
  </Content>
</template>

<script>
import axios from "axios";
export default {
  created() {
    try {
      axios.get("http://192.168.2.129:3000/api/userlist").then(({ data }) => {
        if (data) this.users = data;
        // console.log(typeof data.slice(this.pageSize, data));
      });
    } catch (e) {}
  },
  watch: {
    fUsers(n) {
      // console.log(n.length);
      this.length = n.length;
    },
    current(n) {
      console.log(n);
    }
  },
  data() {
    return {
      search: "",
      columns: [
        {
          title: "ID",
          key: "id",
          width: 60
        },
        {
          title: " ",
          key: "upic",
          width: 70,
          render: (h, { row }) => {
            // console.log(row);
            return h("Avatar", {
              props: {
                src: row.upic
              }
            });
          }
        },
        {
          title: "姓名",
          key: "name",
          minWidth: 120,
          maxWidth: 200
        },
        {
          title: "邮箱",
          key: "email",
          minWidth: 200
        },
        {
          title: "日期",
          key: "date",
          minWidth: 160,
          maxWidth: 360
        },
        {
          title: "管理",
          key: "date",
          width: 100,
          render: (h, { row }) => {
            return h(
              "Button",
              {
                props: {
                  size: "small",
                  type: "warning"
                },
                on: {
                  click: () => {
                    // this.users.splice(row.index, 1);
                    console.log(row);
                  }
                }
              },
              "删除"
            );
          }
        }
      ],
      users: [],
      length: null,
      current: 1,
      pageSize: 10
    };
  },
  computed: {
    fUsers() {
      return this.users.filter(key => {
        return (
          key.email.indexOf(this.search) > -1 ||
          key.name.indexOf(this.search) > -1
        );
      });
    }
  }
};
</script>

<style scoped>
.user {
  box-sizing: border-box;
  padding: 10px;
  width: 100%;
  height: 100%;
  display: flex;
  flex-flow: column nowrap;
}
.search {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.uers-table {
  width: 100%;
  height: auto;
  background: #fff;
  display: flex;
  /* align-items: center; */
  justify-content: center;
  overflow: auto;
}
.user-page {
  width: 100%;
  height: 40px;
  background-color: #fff;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: center;
}
</style>


